var that;
export default {
	data() {
		return {
			modalName: 'topModal',
			imagePreview: false,
			totalprice: 0,
			isSeller: 1,
			uid: 0,
			seller_id: 0,
			task_id: 0,
			title: '',
			isMember: 0,
			modalMPShow: 0,
			baojiaNow: {
				desc: '维修建议会啊实打实'
			},
			files: [],
			baojiao: [],
			seller: false,
			bidding: false,
			baojiaPop: false,
			tabbar: ["描述详情", "报价商家", "服务进程"],
			scrollLeft: 0, //tab标题的滚动条位置
			winHeight: "", //窗口高度
			currentTab: 0, //预设当前项的值
			active: 0,
			orderInfo: {
				address: "",
				audit_reason: "",
				car_back_address: "",
				car_back_time: "",
				car_get_address: "",
				car_get_time: "",
				car_id: 0,
				car_info: "",
				city: "",
				city_code: 110100,
				comment: 0,
				content: "",
				create_at: "",
				customer_reason: "",
				district: "",
				district_code: 110101,
				experss_no: "",
				from_type: 1,
				id: 0,
				image: "",
				is_push: 0,
				latitude: "",
				link_man: "",
				link_mobile: "",
				longitude: "",
				obj_type: 0,
				obj_type2: 0,
				obj_type2_name: "",
				obj_type3: "",
				obj_type3_name: [],
				obj_type_name: "",
				ordersn: "",
				pickup_service: 0,
				predict_price: "",
				predict_time: "0",
				price: "0.00",
				province: "",
				province_code: 110000,
				remark: null,
				seller_id: 0,
				sid_task: 0,
				sort: 0,
				status: 0,
				time: "0",
				title: "",
				uid: 0,
				views: 0
			},
			bidshop: [],
			lookbidlog: {},
			orderprogress: [],
			areaPopshow: true,
			cardCur: 0,
			images: [],
			dotStyle: false,
			towerStart: 0,
			topPop: true,
			height: this.CustomBar,
			items: {}

		}
	},
	onShow() {
		this.isSellers(); //判断是否是商家
		this.isBidding(); //判断是否已报价
	},
	onLoad(e) {
		that = this;
		//  高度自适应
		uni.getSystemInfo({
			success: function(res) {
				let calc = res.windowHeight;
				that.winHeight = calc-100;
			}
		});
		// console.log(e.task_id);
		that.task_id = e.task_id;
		this.seller_id = uni.getStorageSync("seller_id")
		this.uid = uni.getStorageSync("uid")
		let data = {
			task_id: e.task_id
		};
		this.$api.postWithData(this.api.taskDetail, data,
			function callbacks(res) {
				if (res.code == 0) {
					that.$api.msg("当前挂单已关闭或不存在")
					setTimeout(function() {
						uni.navigateBack()
					}, 2000);
					return;
				}
				that.orderInfo = res.data;
				that.images = that.orderInfo.image.split(',');
				that.$api.postWithData(that.api.taskDetailLog, data,
					function callbacks(res) {
						that.bidshop = res.data
						that.$api.postWithData(that.api.taskDetailTimeLine, data,
							function callbacks(res) {
								that.orderprogress = res.data;
							})
					})
			})
	},
	methods: {
		// 滚动切换标签样式
		switchTab: function(e) {
			let that = this;
			this.currentTab = e.detail.current;
			this.checkCor();
		},
		// 点击标题切换当前页时改变样式
		swichNav: function(e) {
			let cur = e.currentTarget.dataset.current;
			if (this.currentTab == cur) {
				return false;
			} else {
				this.currentTab = cur
			}
		},
		//判断当前滚动超过一屏时，设置tab标题滚动条。
		checkCor: function() {
			if (this.currentTab > 3) {
				//这里距离按实际计算
				this.scrollLeft = 300
			} else {
				this.scrollLeft = 0
			}
		},
		showModalMP() {
			// console.log(that.modalMPShow);
			if (that.modalMPShow == 1) {
				that.modalMPShow = 0;
			} else {
				that.modalMPShow = 1;
			}
			// console.log(that.modalMPShow);
		},
		isSellers() {
			this.$api.post(this.api.isSeller,
				function callbacks(res) {
					// if(res.code==1){
					// 	that.isSeller = 1;
					// }else{
					// 	that.isSeller = 0;
					// }
					if (res.code == 2) {
						that.seller = true
					} else {
						that.seller = false
					}
				})
		},
		isBidding() {
			this.$api.postWithData(this.api.isBidding, {
					task_id: that.task_id
				},
				function callbacks(res) {
					// console.log(res.code);
					if (res.code == 0) {
						that.bidding = false
					} else {
						that.bidding = true
					}
				})
		},
		previewImage(images, index) {
			// console.log(images);
			// 预览图片
			uni.previewImage({
				current: index,
				urls: images,
				longPressActions: {
					itemList: ['发送给朋友', '保存图片', '收藏'],
					success: function(data) {
						console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
					},
					fail: function(err) {
						console.log(err.errMsg);
					}
				}
			});
		},
		lookdetail(item) { //查看报价
			this.items = item;
			let data = {
				task_id: item.task_id,
				seller_id: item.seller_id
			};
			this.$api.postWithData(this.api.lookbidlog, data,
				function callbacks(res) {
					that.baojiao = res.data;
					that.baojiao.items = JSON.parse(res.data.items)
					that.baojiaPop = true
					if (res.data.files != null && res.data.files != '') {
						that.files = res.data.files.split(",")
					} else {
						that.files = [];
					}
				})
		},
		cardSwiper(e) {
			this.cardCur = e.detail.current
		},
		bidlogin() {
			uni.navigateTo({
				url: '../mine/h5?url=/index/shop/login'
			})
		},
		navigateTo(page) {
			uni.navigateTo({
				url: page
			})
		},
		navigateToMap(index, info) {
			let latitude, longitude, name, address;
			if (index == 1) {
				latitude = Number(info.latitude)
				longitude = Number(info.longitude)
				name = '位置'
				address = info.address
			} else if (index == 2) {
				latitude = Number(info.car_get_latitude)
				longitude = Number(info.car_get_longitude)
				name = '位置'
				address = info.car_get_address
			} else if (index == 3) {
				latitude = Number(info.car_back_latitude)
				longitude = Number(info.car_back_longitude)
				name = '位置'
				address = info.car_back_address
			}
			uni.openLocation({
				latitude: latitude,
				longitude: longitude,
				name: name,
				address: address,
			})
		},
		activeTab(res) {
			this.active = res.detail.index;
			// console.log(res.detail.index)
			// console.log(res.detail.title)
		},
		submitorder(items) {
			that.baojiaPop = false
			uni.navigateTo({
				url: 'orderpage?task_id=' + items.task_id + '&seller_id=' + items.seller_id
			})
		}
	}
}
